<template>
  <div class="pt-3 pl-4 pr-4">
    <div v-show="ContractTable">
      <div>
        <router-link :to="{ path: '/Müqavilə'}" class="text-white" replace>
          <b-button  variant="primary">Geri</b-button>
        </router-link>
      </div>
      <div class="text-center">
        <h1 class="text-dark">Müqavilələr</h1>
        <button @click="test()">test</button>
      </div>
      <div class="my-5">
        <b-row>
          <b-col class="mt-2" sm="2">
            <b-form-select size="sm"  v-model="selected1" style="font-size:18px" :options="options1"></b-form-select>
          </b-col>
          <b-col class="mt-2" sm="2">
            <b-form-select size="sm" v-model="selected2" style="font-size:18px" :options="options2"></b-form-select>
          </b-col>
          <b-col class="mt-2" sm="2">
            <b-form-select size="sm" v-model="selected3" style="font-size:18px" :options="options3"></b-form-select>
          </b-col>
          <b-col class="mt-2" sm="2">
            <b-form-select size="sm" v-model="selected4" style="font-size:18px" :options="options4"></b-form-select>
          </b-col>
          <b-col>
            <b-form class="text-red navbar-search form-inline"
                   :class="{'navbar-search-dark': type === 'default', 'navbar-search-light': type === 'light'}"
                   id="navbar-search-main">
               <b-form-group class="mb-0 text-white">
                 <b-input-group size="72px" class=" text-dark input-group-alternative input-group-merge">
                   <!-- <div class="search-right"> -->
                      <b-form-input 
                      class="text-dark" 
                      placeholder="Axtar" 
                      type="text"
                      v-model="search"
                      > 
                      </b-form-input>
                      <div class="input-group-append">
                        <span class="input-group-text text-dark"><i class="fas fa-search"></i></span>
                      </div>
                   <!-- </div> -->
                 </b-input-group>
               </b-form-group>
            </b-form>
          </b-col>
        </b-row>
      </div>
      <div class="px-3">
        <b-row class="p-2 bg-light">
          <b-col sm="1"><b>Nömrə</b></b-col>
          <b-col sm="2"><b>Ad,Soyad</b></b-col>
          <b-col sm="2"><b>Tarix</b></b-col>
          <b-col sm="2"><b>Qalıq</b></b-col>
          <b-col sm="2"><b>DEALER</b></b-col>
          <b-col sm="2"><b>CANVASSER</b></b-col>
          <b-col sm="1"><b>Ofis</b></b-col>
        </b-row>
        <b-row v-for="(item,index) in  filterPost" :key="index.id" class="p-2 tab" @click="funk(item,index)">
          <b-col sm="1">{{item.id}}</b-col>
          <b-col sm="2">{{item.Müştəri}}</b-col>
          <b-col sm="2">{{item.Tarix}}</b-col>
          <b-col sm="2"><b>{{item.qalanpul}}</b></b-col>
          <b-col sm="2">{{item.dealer}}</b-col>
          <b-col sm="2">{{item.canvasser}}</b-col>
          <b-col sm="1">{{item.Ofis}}</b-col>
        </b-row>
      </div>
    </div>
    <div v-show="!ContractTable">
      <div>
        <b-button variant="outline-primary" @click="funk1()">Close</b-button>
      </div>
      <div class="pt-2">
        <div class="">
        <b-row align-v="center" slot="header" class="pt-3">
          <b-col lg="12" class="text-center">
            <h1 size="72px"><b>Müqavilə:{{poparry.id}}</b></h1>
          </b-col>
        </b-row>
        <div>
          <div class="mt-5">
            <b-row>
              <b-col lg="4">
                <base-input
                  type="text"
                  label="Tarix:"
                  :disabled="change"
                  v-model="poparry.Tarix"
                  name="user.username"
                >
                </base-input>
              </b-col>
              <b-col lg="4">
                <base-input
                  type="text"
                  :disabled="change"
                  label="Dealer:"
                  v-model="poparry.dealer"
                  name="user.username"
                >
                </base-input>
              </b-col>
               <b-col lg="4">
                <base-input
                  type="text"
                  label="Canvasser:"
                  :disabled="change"
                  v-model="poparry.canvasser"
                  name="user.username"
                >
                </base-input>
              </b-col>
            </b-row>
            <hr class="my-4"/>
            <h6 class="heading-small text-muted mb-4">Müştəri məlumatları</h6>
            <b-row>
              <b-col lg="4">
                <base-input
                  type="text"
                  :disabled="change"
                  label="Müştəri adı,Soyadı,Atasının adı:"
                  placeholder="Müştəri adı,Soyadı,Atasının adı:"
                  v-model="poparry.Müştəri"
                  name="user.username"
                >
                </base-input>
              </b-col>
              <b-col lg="4">
                <base-input
                  type="number"
                  label="Whatsapp Nömrəsi.(məcburidir)."
                  placeholder="Whatsapp Nömrəsi"
                  :disabled="change"
                  required
                  v-model="poparry.userphone1"
                  name="person.userphone1"
                >
                </base-input>
              </b-col>
              <b-col lg="4">
                <base-input
                  type="tel"
                  label="Telefon nömrəsi."
                  placeholder="Telefon nömrəsi"
                  v-model="poparry.userphone2"
                  :disabled="change"
                  name="person.userphone2"
                >
                </base-input>
              </b-col>
            </b-row>
            <b-row>    
              <b-col lg="4">
                <p style="font-weight: 600">
                  Şəxsiyyət vəsiqəsinin şəkili.
                </p>
                <img
                  :src="
                    poparry.userImage == null
                      ? 'img/brand/select.png'
                      : poparry.userImage
                  "
                  :disabled="change"
                  height="138"
                  class="img-responsive d-block text-center navbar-brand-img mt--3"
                />
                <input
                  type="file"
                  ref="file"
                  style="display: none"
                  @change="onChange($event)"
                  class="form-control"
                />
                <b-button
                  v-show="!change"
                  class="mt-1 mb-3"
                  variant="danger"
                  type="button"
                  @click="$refs.file.click()"
                  >Şəkili Dəyiş</b-button
                >
              </b-col>
              <b-col lg="4">
                <base-input
                  type="tel"
                  label="Telefon nömrəsi."
                  placeholder="Telefon nömrəsi"
                  v-model="poparry.userphone3"
                  :disabled="change"
                  name="person.userphone3"
                >
                </base-input>
              </b-col>
              <b-col lg="4">
                <base-input
                  type="tel"
                  label="Telefon nömrəsi."
                  placeholder="Telefon nömrəsi"
                  :disabled="change"
                  v-model="poparry.userphone4"
                  name="person.userphone4"
                >
                </base-input>
              </b-col>
            </b-row>
          </div>
          <div>
            <hr class="my-4"/>
            <h6 class="heading-small text-muted mb-4">Ödəmə məlumatı</h6>
            <div class="">
              <b-row>
                <b-col lg="4">
                  <div>
                    <b-form-group
                      label="ÖDƏNİŞ NÖVÜ"
                      v-slot="{ ariaDescribedby }"
                    >
                      <b-form-radio
                        v-model="poparry.selected"
                        :aria-describedby="ariaDescribedby"
                        name="some-radios"                            
                        value="A"
                        :disabled="swich"                                          
                        >Nəğd</b-form-radio
                      >
                      <b-form-radio
                        v-model="poparry.selected"
                        :aria-describedby="ariaDescribedby"
                        name="some-radios"
                        :disabled="!swich"                                          
                        value="B"
                        >Kredit</b-form-radio
                      >
                    </b-form-group>
                  </div>
                </b-col>
              </b-row>
              <b-row v-show="poparry.selected == 'A'">
                <b-col lg="4">
                  <base-input
                    @keyup="pil($event)"
                    type="number"
                    label="İlkin ödəniş"
                    :disabled="change"
                    placeholder="Məbləğ, AZN"
                    v-model="poparry.nəğdİlkin"
                    name="i"
                  >
                  </base-input>
                </b-col>
                <b-col lg="8">
                  <b-row>
                    <b-col lg="6">
                      <base-input
                        type="date"
                        label="Qalan ödənişin ödənmə tarixi"
                        v-model="poparry.qaliqvaxt"
                        :disabled="change"
                        name="person.qaliqvaxt"
                      >
                      </base-input>
                    </b-col>
                    <b-col lg="6">
                      <base-input
                        label="Qalan məbləğ"
                        type="number"
                        :disabled="change"
                        v-model="poparry.qalanpul"
                        name="person.qalanpul"
                      >
                      </base-input>
                    </b-col>
                  </b-row>
                </b-col>
              </b-row>
            </div>
          </div>
            <b-row>
              <b-col lg="6">
                <hr class="my-4"/>
                <h6 class="heading-small text-muted mb-4">Əlaqə məlumatı</h6>
                <b-form-group
                  label="Ünvan"
                  label-class="form-control-label"
                  :disabled="change"
                  class="mb-0"
                  label-for="about-form-textaria"
                >
                  <b-form-textarea
                    rows="4"
                    v-model="person.useradress"
                    id="about-form-textaria"
                    placeholder="Müştərinin ünvanı"
                    :disabled="change"
                  ></b-form-textarea>
                </b-form-group>
              </b-col>
              <b-col lg="6">
                <hr class="my-4" />
                <h6 class="heading-small text-muted mb-4">Müştəri haqqında</h6>
                <b-form-group
                  label="Müştəri haqqında"
                  label-for="about-form-textaria"
                >
                  <b-form-textarea
                    rows="4"
                    v-model="poparry.usernote"
                    :disabled="change"
                    id="about-form-textaria"
                    placeholder="Haqqında"
                  ></b-form-textarea>
                </b-form-group>
              </b-col>
            </b-row>
        </div>
      </div>  
      <div class="" v-show="poparry.selected == 'B'">
        <div class="mt-5">
          <b-row class="text-black" size="lg">
            <b-col lg="3">
              <base-input
                @keyup="voll()"
                type="number"
                label="İlkin ödəniş"
                :disabled="change"
                v-model="poparry.kreditİlkin"
                name="b"
              ></base-input>
              <base-input
                type="number"
                label="Neçə ayda ödəniləcək"
                v-model="poparry.kreditmuddeti"
                :disabled="change"
                max=24
                @keyup="fill($event)"
                name="c"
              ></base-input>
              <base-input
                type="date"
                label="İlkin ödənişin tarixi"
                v-model="poparry.Tarix"
                :disabled="change"
                name="b"
              ></base-input>
              <b-button  v-show="!change" variant="outline-warning" @click="funk()">Daxil et</b-button>
            </b-col>
            <b-col lg="6" class="p-0" v-show="block" style="display:flex">
              <b-col lg="6">
                  <span>Ödənişlər</span>
                <div  v-for="(item,index) in poparry.ödəmələr" :key="item.id">
                  <b-form-input
                    class="ip mt-2 text-center"
                    @keyup="pull($event, item)"
                    v-model="poparry.ödəmələr[index]"
                    :disabled="change"
                    type="number"
                  ></b-form-input>
                </div>
              </b-col>
              <b-col lg="6">
                  <span>Tarixlər:</span>
                <div v-for="(item,index) in poparry.vaxtlar" :key="item.id">
                  <b-form-input
                    class="pin mt-2 text-center"
                    @change="showDateTime($event,item)"
                    v-model="poparry.vaxtlar[index]"
                    :disabled="change"
                    type="date"
                  ></b-form-input>
                </div>
              </b-col>
            </b-col>
          </b-row>
        </div>
      </div>
      </div>
      <div>
        <b-row class="mt-5 text-center" v-show="change">
          <b-col lg="12">
            <b-button
              style="width: 150px"
              variant="primary"
              @click="changed()"
              >Dəyiş
            </b-button>
            <b-button
              style="width: 150px"
              variant="primary"
              @click="deleteFunk()"
              >Sil
            </b-button>
          </b-col>
        </b-row>
      </div>
      <div  v-show="!change">
        <b-row class="mt-5 text-center">
          <b-col lg="12">
            <b-button
              style="width: 150px"
              variant="success"
              @click="confirmation()"
              >Təsdiqlə
            </b-button>
          </b-col>
        </b-row>
      </div>
    </div>
  </div>
</template>
<script>
var today = new Date();
var dd = String(today.getDate()).padStart(2, '0');
var mm = String(today.getMonth() + 1).padStart(2, '0'); //January is 0!
var yyyy = today.getFullYear();
today = yyyy+'-' +mm+ '-' + dd;
import Vue from "vue";
import axios from "axios";
import VueAxios from "vue-axios";
Vue.use(VueAxios, axios);
  export default {
    data() {  
      return {
        a:'',
        search:'',
        selected1: null,
        selected2: null,
        selected3:null,
        selected4:null,
        options1: [
          { value: null, text: 'Müqavilələr'},
          { value: 'A', text: 'Nəğd' },
          { value: 'B', text: 'Kredit' }
        ],
        options2: [
          { value: null, text: 'Ödəniş'},
          { value: 'A', text: 'Bitmiş' },
          { value: 'B', text: 'Davamedən' },
        ],
        options3: [
          { value: null, text: 'Ofislər'},
          { value: 'a', text: 'Bakı' },
          { value: 'b', text: 'Gəncə' },
          { value: 'c', text: 'Xaçmaz' },
          { value: 'd', text: 'Şəki' },
          { value: 'e', text: 'Yevlax' },
          { value: 'f', text: 'İmişli' },
          { value: 'g', text: 'Cəlilabad' },
          { value: 'h', text: 'Göyçay'}
        ],
        options4: [
          { value: null, text: 'Tarix'},
          { value: 'a', text: 'Bakı' },
          { value: 'b', text: 'Gəncə' },
          { value: 'c', text: 'Xaçmaz' },
          { value: 'c', text: 'Şəki' },
          { value: 'c', text: 'Yevlax' },
          { value: 'c', text: 'İmişli' },
          { value: 'c', text: 'Cəlilabad' },
          { value: 'd', text: 'Göyçay', disabled: true }
        ],
        ContractTable:true,
        items: [],
        delete:[],
        poparry:[],
        status: 'not_accepted',
        dateFormField: [],
        dealer: "",
        canvasser: "",
        person: {
          Müştəri: "",
          Tarix:today,
          Ofis: "Bakı",
        },
        name: ["ilkin ödəniş"],
        user: {
          userphone1: "",
          userphone2: "",
          userphone3: "",
          userphone4: "",
          userImage: null,
          useradress: "",
          usernote: "",
        },
        selected: "",
        person1: true,
        person2: false,
        person3: false,
        person4: false,
        person5: false,
        person6: false,
        cash: false,
        buydata: true,
        block:true,
        first:[],
        a: 1890,
        b: null,
        c: null,
        p: 10,
        m: null,
        n: null,
        k: null,
        kk:2,
        f: null,
        d: null,
        i: "",
        qaliqvaxt: null,
        q: null,
        sabit: null,
        change:true,
        swich:false
      }
    },  
    mounted(){
      fetch("http://localhost:3000/person")
        .then(res=>res.json())
        .then(data=>this.items=data)
        .catch(err=>console.log(console.err.message));
    },
    methods:{
      funk(item,index){
        this.swich=false
        this.ContractTable =! this.ContractTable
        this.poparry=item
        if(item.selected=="B"){
          this.swich=true
        }
      },
      funk1(){
        this.ContractTable =! this.ContractTable
        this.change=true
      },
      changed(){
        this.change=false
      },
      onChange(e) {
        const file = e.target.files[0];
        this.poparry.userImage = URL.createObjectURL(file);
      },
      getData(){
        this.axios.get("http://localhost:3000/person").then((result)=>{
          this.items=result.data  
        })
      },
      deleteFunk(){
        console.log(this.poparry.id)
        if(window.confirm('Sil')){
          this.axios.delete("http://localhost:3000/person/"+this.poparry.id).then(()=>{
            this.getData()
            this.funk1()
          })
          alert('Silindi')
        }
        else{
          alert("Ləğv olundu")
        }
  
      },
      confirmation(){
        this.change=true;
        console.log(this.poparry)
        this.axios.post("http://localhost:3000/person/"+this.poparry.id, this.poparry)
          .then((result) => {console.log(result);
        });  
      },
      filterPost(){
      },
      test(){
        console.log(this.items)
      }
    },
    computed:{
      filterPost(){
        return this.items.filter((item)=> {
          if(this.selected1=='A'){
            return item.selected.includes('A') && item.Müştəri.match(this.search)
          }
          else if(this.selected1=='B'){
            return item.selected.includes('B') && item.Müştəri.match(this.search)
          }
          else if(this.selected2=='A'){
            return item.qalanpul.includes(990)
          }
          else if(this.selected3=='a'){
             return item.Ofis.includes('a') && item.Müştəri.match(this.search)
          }
          else if(this.selected3=='b'){
             return item.Ofis.includes('b') && item.Müştəri.match(this.search)
          }
          else{
            return this.items
          }
        })
      }
    }
  }
</script>

<style lang="postcss" scoped>
.form-inline{
  display: flex;
  justify-content: center;
  color: white;
}
.tab:nth-child(odd){
  background:rgb(236, 227, 227);
}
</style>