<template>
  <div>
    <b-form id="gyus" @submit="postDate" method="post">
      <div class="text-white pl-4 pr-4" v-show="person1">
        <div>
          <b-row align-v="center" slot="header" class="pt-3">
            <b-col>
              <router-link
                :to="{ path: '/Müqavilə' }"
                class="text-white"
                replace
              >
                <b-button ref="submit" variant="primary">Geri</b-button>
              </router-link>
            </b-col>
          </b-row>
          <div class="pb-lg-5 pt-5">
            <b-row>
              <b-col lg="6" md="12">
                <b-row>
                  <b-col>
                    <base-input
                      type="date"
                      :required="true"
                      lang="az-latin-AZ"
                      label="TARİX:"
                      placeholder="Məhsulun adı:"
                      v-model="person.Tarix"
                      name="product.selldata"
                    >
                    </base-input>
                  </b-col>
                  <b-col lg="12">
                    <base-input
                      type="text"
                      label="DEALER"
                      required="true"
                      placeholder="Adı,Soyadı,Ata adı;"
                      v-model="person.dealer"
                      name="dealer"
                    >
                    </base-input>
                  </b-col>
                  <b-col lg="12" md="12">
                    <base-input
                      type="text"
                      label="CANVASSER"
                      placeholder="Adı,Soyadı,Ata adı;"
                      required
                      v-model="person.canvasser"
                      name="canvasser"
                    >
                    </base-input>
                  </b-col>
                </b-row>
              </b-col>
            </b-row>
            <div class="">
              <b-row class="mt-lg-7 mt-6 text-center">
                <b-col lg="12">
                  <b-button
                    style="width: 150px"
                    variant="primary"
                    @click="funk1()"
                    >İrəli
                  </b-button>
                </b-col>
              </b-row>
            </div>
          </div>
        </div>
      </div>
      <div class="pl-4 pr-4" v-show="person2">
        <b-row align-v="center" slot="header" class="pt-3">
          <b-col>
            <!-- <router-link :to="{ path: '/maps'}" class="text-white" replace> -->
            <b-button @click="funk1()" variant="primary">Geri</b-button>
            <!-- </router-link> -->
          </b-col>
          <b-col lg="12" class="text-center">
            <h1 size="72px"><b>Yeni Müqavilə</b></h1>
          </b-col>
        </b-row>
        <div>
          <div class="">
            <h6 class="heading-small text-muted mb-4">Müştəri məlumatları</h6>
            <b-row>
              <b-col lg="4">
                <base-input
                  type="text"
                  required
                  label="Müştəri adı,Soyadı,Atasının adı:"
                  placeholder="Müştəri adı,Soyadı,Atasının adı:"
                  v-model="person.Müştəri"
                  name="user.username"
                >
                </base-input>
              </b-col>
              <b-col lg="4">
                <base-input
                  type="number"
                  label="Whatsapp Nömrəsi.(məcburidir)."
                  placeholder="Whatsapp Nömrəsi"
                  required
                  v-model="person.userphone1"
                  name="person.userphone1"
                >
                </base-input>
              </b-col>
              <b-col lg="4">
                <base-input
                  type="tel"
                  label="Telefon nömrəsi."
                  placeholder="Telefon nömrəsi"
                  v-model="person.userphone2"
                  name="person.userphone2"
                >
                </base-input>
              </b-col>
            </b-row>
            <b-row>    
              <b-col lg="4">
                <p style="font-weight: 600">
                  Şəxsiyyət vəsiqəsinin şəkili.
                </p>
                <img
                  :src="
                    person.userImage == null
                      ? 'img/brand/select.png'
                      : person.userImage
                  "
                  @click="$refs.file.click()"
                  height="138"
                  class="img-responsive d-block text-center navbar-brand-img mt--3"
                  required
                />
                <input
                  ref="file"
                  type="file"
                  style="display: none"
                  @change="onChange($event)"
                  class="form-control"
                  required
                />
                <b-button
                  class="mt-1 mb-3"
                  variant="danger"
                  type="button"
                  @click="$refs.file.click()"
                  >Şəkil Əlavə et</b-button
                >
              </b-col>
              <b-col lg="4">
                <base-input
                  type="tel"
                  label="Telefon nömrəsi."
                  placeholder="Telefon nömrəsi"
                  v-model="person.userphone3"
                  name="person.userphone3"
                >
                </base-input>
              </b-col>
              <b-col lg="4">
                <base-input
                  type="tel"
                  label="Telefon nömrəsi."
                  placeholder="Telefon nömrəsi"
                  v-model="person.userphone4"
                  name="person.userphone4"
                >
                </base-input>
              </b-col>
            </b-row>
          </div>
          <div>
            <hr class="my-4"/>
            <h6 class="heading-small text-muted mb-4">Ödəmə məlumatı</h6>
            <div class="">
              <b-row>
                <b-col lg="4">
                  <div>
                    <b-form-group
                      label="ÖDƏNİŞ NÖVÜ"
                      v-slot="{ ariaDescribedby }"
                    >
                      <b-form-radio
                        v-model="person.selected"
                        :aria-describedby="ariaDescribedby"
                        name="some-radios"
                        value="A"
                        required
                        >Nəğd</b-form-radio
                      >
                      <b-form-radio
                        v-model="person.selected"
                        :aria-describedby="ariaDescribedby"
                        name="some-radios"
                        value="B"
                        required
                        >Kredit</b-form-radio
                      >
                    </b-form-group>
                  </div>
                </b-col>
              </b-row>
              <b-row v-show="person.selected == 'A'">
                <b-col lg="4">
                  <base-input
                    @keyup="pil($event)"
                    type="number"
                    label="İlkin ödəniş"
                    placeholder="Məbləğ, AZN"
                    v-model="person.nəğdİlkin"
                    name="i"
                  >
                  </base-input>
                </b-col>
                <b-col lg="8" v-show="cash">
                  <b-row>
                    <b-col lg="6">
                      <base-input
                        type="date"
                        label="Qalan ödənişin ödənmə tarixi"
                        v-model="person.qaliqvaxt"
                        name="person.qaliqvaxt"
                      >
                      </base-input>
                    </b-col>
                    <b-col lg="6">
                      <base-input
                        label="Qalan məbləğ"
                        type="number"
                        readonly
                        v-model="person.qalanpul"
                        name="person.qalanpul"
                      >
                      </base-input>
                    </b-col>
                  </b-row>
                </b-col>
              </b-row>
            </div>
          </div>
            <b-row>
              <b-col lg="6">
                <hr class="my-4"/>
                <h6 class="heading-small text-muted mb-4">Əlaqə məlumatı</h6>
                <b-form-group
                  label="Ünvan"
                  label-class="form-control-label"
                  class="mb-0"
                  label-for="about-form-textaria"
                >
                  <b-form-textarea
                    rows="4"
                    v-model="person.useradress"
                    id="about-form-textaria"
                    placeholder="Müştərinin ünvanı"
                  ></b-form-textarea>
                </b-form-group>
              </b-col>
              <b-col lg="6">
                <hr class="my-4" />
                <h6 class="heading-small text-muted mb-4">Müştəri haqqında</h6>
                <b-form-group
                  label="Müştəri haqqında"
                  label-for="about-form-textaria"
                >
                  <b-form-textarea
                    rows="4"
                    v-model="person.usernote"
                    id="about-form-textaria"
                    placeholder="Haqqında"
                  ></b-form-textarea>
                </b-form-group>
              </b-col>
            </b-row>
            <b-row class="mt-3 text-center">
              <b-col lg="12">
                <b-button
                  style="width: 150px"
                  variant="primary"
                  @click="funk3()"
                  >İrəli
                </b-button>
              </b-col>
            </b-row>
        </div>
      </div>  
      <div class="pl-4 pr-4" v-show="person4">
        <b-row align-v="center pt-3" slot="header">
          <b-col>
            <!-- <router-link :to="{ path: '/maps'}" class="text-white" replace> -->
            <b-button variant="primary" @click="funk3()">Geri</b-button>
            <!-- </router-link> -->
          </b-col>
        </b-row>
        <b-row align-v="center" slot="header">
          <b-col cols="12" class="text-center">
            <h1 size="72px"><b>Kredit</b></h1>
          </b-col>
        </b-row>
        <div class="mt-5 ">
          <b-row class="text-black" size="lg">
            <b-col lg="4" class="mb-4">
              <b-col lg="12">
                <base-input
                  type="date"
                  v-model="person.Tarix"
                  label="Tarix"
                  name="b"
                ></base-input>
                <base-input
                  @keyup="voll()"
                  type="number"
                  label="İlkin ödəniş"
                  v-model="person.kreditİlkin"
                  name="b"
                ></base-input>
                <base-input
                  type="number"
                  label="Neçə ayda ödəniləcək"
                  v-model="person.kreditmuddeti"
                  max=24
                  @keyup="fill($event)"
                  name="c"
                ></base-input>
                <b-button variant="outline-warning" @click="funk()">Daxil et</b-button>
              </b-col>
            </b-col>
            <b-col lg="8">
              <div v-show="block" >
                <b-col lg="6">
                  <span class="mr-4">Tarixlər:</span>
                  <span class="ml-8">Ödənişlər:</span>
                </b-col>
                <b-col lg="12" v-for="item in Number(p)" :key="item.id">
                  <b-form-input
                    class="pin lg-ml-4 mt-2"
                    @change="showDateTime($event,item)"
                    label="1"
                    type="date"
                    style="width: 200px; display: inline-block"
                  ></b-form-input>
                  <b-form-input
                    class="ip ml-2"
                    @keyup="pull($event, item)"
                    :placeholder="p.index"
                    type="number"
                    style="width: 200px; display: inline-block"
                  ></b-form-input>
                </b-col>
              </div>
            </b-col>
          </b-row>
        </div>
        <b-row class="mt-5 text-center">
          <b-col lg="12">
            <b-button
              style="width: 150px"
              variant="primary"
              :disabled="buydata"
              @click="funk5()"
              >İrəli
            </b-button>
          </b-col>
        </b-row>
      </div>
      <div class="pl-4 pr-4" v-show="person5">
        <div>
          <b-row align-v="center pt-3" slot="header">
            <b-col>
              <!-- <router-link :to="{ path: '/maps'}" class="text-white" replace> -->
              <b-button variant="primary" @click="funk4()">Geri</b-button>
              <!-- </router-link> -->
            </b-col>
          </b-row>
          <div class="mt-5">
            <b-row>
              <b-col lg="12">
                 <b-form-checkbox
                    id="checkbox-1"
                    v-model="status"
                    class="pb-3"
                    size="lg"
                  >
                     <strong>ŞƏRTLƏRİ QƏBUL EDİN.
                       <router-link :to="{path:'/Şərtlər'}">
                        BAX.
                </router-link></strong>
                  </b-form-checkbox>
              </b-col>
              <b-col lg="12">
                 <b-form-checkbox
                    v-model="a"
                    class="pb-3"
                    size="lg"
                  >
                     <strong>MÜQAVİLƏ.
                       <router-link :to="{path:'/tamMüqavilə'}">
                        BAX.
                </router-link></strong>
                  </b-form-checkbox>
              </b-col>
            </b-row>
          </div>
          <b-row class="mt-9 text-center">
            <b-col lg="12">
              <b-button
                style="width: 150px"
                variant="success"
                type="submit"
                from="gyus"
                @click="$refs.submit.click()"
                >Təsdiq
              </b-button>
            </b-col>
          </b-row>
        </div>
      </div>
    </b-form>
  </div>
</template>
<script>
var today = new Date();
var dd = String(today.getDate()).padStart(2, '0');
var mm = String(today.getMonth() + 1).padStart(2, '0'); //January is 0!
var yyyy = today.getFullYear();
today = yyyy+'-' +mm+ '-' + dd;
console.log(today)
import Vue from "vue";
import axios from "axios";
import VueAxios from "vue-axios";
Vue.use(VueAxios, axios);
import BaseInput from "../../components/Inputs/BaseInput.vue";
export default {
  components: { BaseInput },
  data() {
    return {
      status: 'not_accepted',
      dateFormField: [],
      person: {
        dealer: "",
        canvasser: "",
        Müştəri: "",
        Tarix:today,
        Ofis: "Bakı",
        userphone1: "",
        userphone2: "",
        userphone3: "",
        userphone4: "",
        useradress: "",
        usernote: "",
        userImage: null,
        selected: "",
        nəğdİlkin:"",
        kreditmuddeti: null,
        kreditİlkin:null,
        qalanpul: "",
        qaliqvaxt: null,
        aylar:[],
        ödəmələr:[],
        vaxtlar:[]
      },
      name: ["ilkin ödəniş"],
      person1: true,
      person2: false,
      person3: false,
      person4: false,
      person5: false,
      person6: false,
      cash: false,
      buydata: true,
      block: false,
      a: 1890,
      p: null,
      m: null,
      n: null,
      k: null,
      kk:null,
      f: null,
      d: null,
      sabit: null,
    };
  },
  methods: {
    showDateTime(data,k){
          var azi = data.split("-");
          var il=Number(azi[0])
          var ay=Number(azi[1])
          var gun=azi[2]
          if(gun==31){
            alert()
            gun--
          }
          else{
            gun=gun
          }
          for (var i = k-1; i <=this.person.kreditmuddeti; i++) {
              if(ay<=9){
                if((ay==2)&&(gun==29||gun==30)){
                  azi=il+'-'+'0'+ay+'-'+'28'
                }
                else{
                  azi=il+'-'+'0'+ay+'-'+gun
                  console.log(azi)
                }
              }
              else if(ay==13){
                ay=1
                il++
                azi=il+'-'+'0'+ay+'-'+gun
                  console.log(azi)
              }
              else{
                azi=il+'-'+ay+'-'+gun
                if(ay==12){
                  ay=0
                  il++
                }
              }
              ay++
              document.getElementsByClassName('pin')[i].value=azi
          }
    },
    teqvim(e) {
      alert(e.target.value);
    },
    misus(){
       
    },
    pull(e, item) {
      if(e.target.value<0){
        e.target.value=0
      }
        for (var i = item; i < this.p - 1; i++) {
          document.getElementsByClassName("ip")[i].value =e.target.value;
        }
        for (var i =0; i < this.p - 1; i++) {
          this.f += Number(document.getElementsByClassName("ip")[i].value);
        }
        document.getElementsByClassName("ip")[this.p - 1].value =
        Number(this.a) - (Number(this.f) + Number(this.person.kreditİlkin));
        if(document.getElementsByClassName("ip")[this.p - 1].value<0){
          this.kk=Math.ceil(Math.abs(document.getElementsByClassName("ip")[this.p - 1].value)/e.target.value)
          this.f = null;
          this.p=this.p-this.kk
          this.person.kreditmuddeti=this.person.kreditmuddeti-this.kk
          for (var i =0; i < this.p - 1; i++) {
            this.f += Number(document.getElementsByClassName("ip")[i].value);
          }
          document.getElementsByClassName("ip")[this.p - 1].value =
          Number(this.a) - (Number(this.f) + Number(this.person.kreditİlkin));
        }
        this.f = null;
    },
    pil(e) {
      if(Number(e.target.value)>Number(1890)){
        this.person.nəğdİlkin=1890
        this.cash = false;
      }
      else if (e.target.value == this.a) {
        this.cash = false;
      } else {
        this.cash = true;
        this.person.qalanpul = Number(this.a) - Number(this.person.nəğdİlkin);
      }
    },
    funk1() {
      if (this.canvasser != "" && this.dealer != "") {
        this.person1 = !this.person1;
        this.person2 = !this.person2;
      } else {
        alert("Ad Soyad daxil edin.");
      }
    },
    funk2() {
      if (
        this.person.Müştəri != "" &&
        this.person.userphone1 != "" &&
        this.person.userImage != null
      ) {
        this.person2 = !this.person2;
        this.person3 = !this.person3;
      } else {
        alert(
          "Ad,Soyad,Whatsapp nömrəsinin və ya Şəxsiyyət vəsiqəsinin düzgün daxil olduguna əmin ol!"
        );
      }
    },
    funk3() {
      if (this.person.Tarix != null &&this.person.selected != "" &&this.person.selected == "A") {
        if ((this.person.nəğdİlkin != "" && this.person.qaliqvaxt != null) || this.nəğdİlkin == 1890) {
          this.person2 = !this.person2;
          this.person5 = !this.person5;
        } else {
          alert("İlkin ödıniş və son tarix daxil edin.");
        }
      } else if (this.person.Tarix != null && this.person.selected == "B") {
        this.person2 = !this.person2;
        this.person4 = !this.person4;
      } else {
        alert("Satış Tarixini və ya Ödəmə üsulunu qeyd edin");
      }
    },
    funk4() {
      if(this.person.selected == "A"){
        this.person2 = !this.person2;
        this.person5 = !this.person5;
      }
      else{
        this.person4 = !this.person4;
        this.person5 = !this.person5;
      }
    },
    funk5() {
      this.person.ödəmələr=[]
      this.person.vaxtlar=[]
      this.person.aylar=[]
      if(this.person.selected=='B'){
        this.person.qalanpul=this.a-this.person.kreditİlkin
      }
      for (var i = 0; i < this.p; i++){
        this.person.vaxtlar.push(document.getElementsByClassName("pin")[i].value)
        this.person.ödəmələr.push(document.getElementsByClassName("ip")[i].value)
      }
      console.log(this.person.vaxtlar)
      console.log(this.person.aylar)
      console.log(this.person.ödəmələr)
      this.person4 = !this.person4;
      this.person5 = !this.person5;
    },
    voll() {
      this.block = false;
      this.buydata = true;
    },
    fill(e) {
      if(this.person.kreditmuddeti>24){
        this.person.kreditmuddeti=24
      }
      this.p = Number(this.person.kreditmuddeti);
      this.block = false;
      this.buydata = true;
    },
    funk() {
      if (Number(this.p) > 3) {
        (this.block = true),
        (this.m = Math.ceil((Number(this.a) - Number(this.person.kreditİlkin)) / (Number(this.p))));
        for (var i = 0; i < this.p-1; i++) {
          document.getElementsByClassName("ip")[i].value = this.m;
          this.f += Number(document.getElementsByClassName("ip")[i].value);
        }
        document.getElementsByClassName("ip")[this.p - 1].value =
          Number(this.a) - (Number(this.f) + Number(this.person.kreditİlkin));
        this.f = 0;
        this.k = this.m;
        this.buydata = false;
      } else if (Number(this.p) > 0) {
        alert("Ödəmə Müddəti Ən Azı 3 Ay ola bilər.");
      }
      else {
        alert("Ödəmə Müddətini Qeyd Edin.");
      }
    },
    onChange(e) {
      const file = e.target.files[0];
      this.person.userImage = URL.createObjectURL(file);
    },
    Təsdiq2() {
      if (this.person.kreditmuddeti != null) {
        this.person1 = true;
        this.person4 = false;
        this.person5 = false;
        this.data == null;
      }
    },
    postDate(e) {
        this.axios.post("http://localhost:3000/person/", this.person)
          .then((result) => {console.log(this.person);
      });  
      e.preventDefault();
    },
  },
};
</script>
<style scoped>
.fade-enter {
  opacity: 0;
}
.fade-enter-active {
  transition: opacity 1s;
  opacity: 1;
}
</style>